html {
    font-family: sans-serif;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%
}
body {
    margin: 0
}
.ih-item.circle.effect {
    margin: 0 auto;
    -webkit-perspective: 900px;
    perspective: 900px;
}
.ih-item.circle.effect .img {
    z-index: 11;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}
.ih-item.circle.effect .info {
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}
.ih-item.circle.effect .info .info-back {
    opacity: 1;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    background: #333333;
}
.ih-item.circle.effect .info h2 {
    color: #fff;    
    position: relative;
    font-size: 18px;
    margin: 0 auto;
    padding-top: 30px;
    height: 30px;
    text-shadow: 0 0 1px white, 0 1px 2px rgba(0, 0, 0, 0.3);
}
.ih-item.circle.effect .info p {
    color: #bbb;
    padding: 0px 0px 0px 0px;
    font-style: italic;
    padding-left: 0px;
    font-size: 10px;
}
.ih-item.circle.effect.bottom_to_top .img {
    -webkit-transform-origin: 50% 0;
    transform-origin: 50% 0;
}
.ih-item.circle.effect.bottom_to_top a:hover .img {
    -webkit-transform: rotate3d(1, 0, 0, 180deg);
    transform: rotate3d(1, 0, 0, 180deg);
}
.ih-item.circle.effect.top_to_bottom .img {
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
}
.ih-item.circle.effect.top_to_bottom a:hover .img {
    -webkit-transform: rotate3d(1, 0, 0, -180deg);
    transform: rotate3d(1, 0, 0, -180deg);
}
.ih-item.circle.effect.left_to_right .img {
    -webkit-transform-origin: 100% 50%;
    transform-origin: 100% 50%;
}
.ih-item.circle.effect.left_to_right a:hover .img {
    -webkit-transform: rotate3d(0, 1, 0, 180deg);
    transform: rotate3d(0, 1, 0, 180deg);
}
.ih-item.circle.effect.right_to_left .img {
    -webkit-transform-origin: 0% 50%;
    transform-origin: 0% 50%;
}
.ih-item.circle.effect.right_to_left a:hover .img {
    -webkit-transform: rotate3d(0, 1, 0, -180deg);
    transform: rotate3d(0, 1, 0, -180deg);
}
.ih-item a {
    color: #333;
}
.ih-item a:hover {
    text-decoration: none;
}
.ih-item img {
    width: 100%;
    height: 100%;
}
.ih-item.circle {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 50%;
}
.ih-item.circle .img {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 50%;
}
.ih-item.circle .img:before {
    position: absolute;
    display: block;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-transition: all 0.35s ease-in-out;
    transition: all 0.35s ease-in-out;
}
.ih-item.circle .img img {
    border-radius: 50%;
}
.ih-item.circle .info {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    border-radius: 50%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
@media all and (max-width: 780px) {
   .ih-item.circle .img {
      position: relative;
      width: 100px;
      height: 100px;
      /*margin-top: 20px;*/
      border-radius: 50%;
  }
  .ih-item.circle {
      position: relative;
      width: 100px;
      height: 100px;
      border-radius: 50%;
  }
  .ih-item.circle .info .info-back h2{
    font-size: 0.9em;
  }
}
/*-end*/
/*-start*/
nav a {
  position: relative;
  display: inline-block;
  margin-top: 15px;
  margin-right: 20px;
}
.nav-menu-item a {
  padding: 0px;
  color: #999;
  font-weight: 700;
  font-size: .8em;
  text-shadow: none;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.nav-menu-item a::before,
.nav-menu-item a::after {
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;
  background: #fff;
  content: '';
  opacity: 0;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform: translateY(-10px);
  transform: translateY(-10px);
}
.nav-menu-item a::before {
  top: 0px;
}
.nav-menu-item a::after {
  bottom: -6px;
  -webkit-transform: translateY(5px);
  transform: translateY(5px);
}
.nav-menu-item a:hover{
  color: #fff;
}
.nav-menu-item a:hover::before,
.nav-menu-item a:hover::after {
  opacity: 1;
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
}
/*-end*/
